  .colorchange{
    -webkit-animation: colorchange 1s infinite;
            animation: colorchange 1s infinite;
  }
   @-webkit-keyframes colorchange{ 
    0%{color: red;}
    25%{-webkit-columns: #bf55ec;columns: #bf55ec;}
    50%{color: white;}
    100%{color: #bf55ec;}
  }
   @keyframes colorchange{ 
    0%{color: red;}
    25%{-webkit-columns: #bf55ec;columns: #bf55ec;}
    50%{color: white;}
    100%{color: #bf55ec;}
  }

  .animate{
    -webkit-animation: 0.5s animate__rubberBand;
            animation: 0.5s animate__rubberBand;
  }  


  #header .animated {
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
  }
  @-webkit-keyframes up-down {
    0% {
      transform: translateY(10px);
    }
  
    100% {
      transform: translateY(-10px);
    }
  }
  @keyframes up-down {
    0% {
      transform: translateY(10px);
    }
  
    100% {
      transform: translateY(-10px);
    }
  }

 
